import { Component, OnInit, Input, SimpleChanges, OnChanges } from '@angular/core';

import {
    CateringOrder,
    CateringOrderStatus,
} from 'src/app/services/data/catering/catering-order.class';
import { ANIMATION_SHOW_CONTRACT_EXPAND } from 'src/app/shared/globals/angular-animations';
import { ApplicationService } from 'src/app/services/app.service';
import { Space } from 'src/app/services/data/spaces/space.class';
import { Identity, HashMap } from 'src/app/shared/utilities/types.utilities';
import { MatDialog } from '@angular/material/dialog';
import { OrderDetailsModalComponent } from '../../overlays/order-details-modal/order-details-modal.component';

@Component({
    selector: 'a-catering-orders-item',
    templateUrl: './item.component.html',
    styleUrls: ['./item.component.scss'],
    animations: [ANIMATION_SHOW_CONTRACT_EXPAND],
})
export class CateringOrdersItemComponent implements OnChanges {
    /** Catering order details to display */
    @Input() public order: CateringOrder;
    /** Search filter string */
    @Input() public search: string;
    /** Whether order items should be shown */
    public show_items: boolean;
    /** Actively selected status */
    public active_status: CateringOrderStatus;
    /** Whether status is being updated */
    public loading: boolean;

    public get search_matches(): HashMap<boolean> {
        const map = {};
        const filter = this.search.toLowerCase();
        this.order.items.forEach(item => {
            map[item.id] = item.name.toLowerCase().indexOf(filter) >= 0;
        });
        return map;
    }

    public statuses: Identity[] = [
        { id: 'accepted', name: 'Accepted', icon: { class: 'custom-accepted' }, colour: '#3996B6' },
        {
            id: 'preparing',
            name: 'Preparing',
            icon: { class: 'custom-preparing' },
            colour: '#E7536B',
        },
        { id: 'ready', name: 'Ready', icon: { class: 'custom-ready' }, colour: '#FFD028' },
        {
            id: 'delivered',
            name: 'Delivered',
            icon: { class: 'custom-delivered' },
            colour: '#75BB43',
        },
        {
            id: 'cancelled',
            name: 'Cancelled',
            icon: { class: 'custom-cancelled' },
            colour: '#747474',
        },
    ];

    /** Details for the active status */
    public get status_details(): Identity {
        return this.statuses.find((status) => status.id === this.active_status) || ({} as any);
    }

    public get space(): Space {
        return this._service.Spaces.find(this.order.location_id);
    }

    constructor(private _service: ApplicationService, private _dialog: MatDialog) {}

    public ngOnChanges(changes: SimpleChanges) {
        /* istanbul ignore else */
        if (changes.order && this.order) {
            this.active_status = this.order.status;
        }
    }

    /** Update the status of the order in the booking */
    public async updateStatus(): Promise<void> {
        const booking = this.order.booking;
        /* istanbul ignore else */
        if (booking) {
            this.loading = true;
            const catering = [...booking.catering];
            catering.splice(
                catering.findIndex((order) => order.id === this.order.id),
                1,
                new CateringOrder({ ...this.order, status: this.active_status })
            );
            booking.storePendingChange('catering', catering);
            await booking.save().catch((err) => {
                this._service.notifyError(
                    `Error updating order status. Error: ${err.message || err}`
                );
                this.active_status = this.order.status;
                this.loading = false;
            });
            this.loading = false;
        }
    }

    /** Open modal to view catering order details */
    public viewOrderDetails(): void {
        this._dialog.open(OrderDetailsModalComponent, {
            maxHeight: '95vh',
            maxWidth: '95vw',
            data: {
                order: this.order,
                booking: this.order.booking
            }
        });
    }
}
