import { Component, OnInit, Input, SimpleChanges, OnChanges } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { MatDialog } from '@angular/material/dialog';
import { Observable, Subject, of } from 'rxjs';
import {
    debounceTime,
    distinctUntilChanged,
    switchMap,
    catchError,
    map,
    first,
} from 'rxjs/operators';

import { CateringOrder } from 'src/app/services/data/catering/catering-order.class';
import { ApplicationService } from 'src/app/services/app.service';
import { BaseDirective } from 'src/app/shared/base.directive';
import { Space } from 'src/app/services/data/spaces/space.class';
import { Building } from 'src/app/services/data/organisation/building.class';
import { unique } from 'src/app/shared/utilities/general.utilities';
import { Booking } from 'src/app/services/data/bookings/booking.class';
import { CateringCategory } from 'src/app/services/data/catering/catering-category.class';
import { OrderDetailsModalComponent } from '../overlays/order-details-modal/order-details-modal.component';

import * as dayjs from 'dayjs';

@Component({
    selector: 'a-catering-orders',
    templateUrl: './orders.component.html',
    styleUrls: ['./orders.component.scss'],
})
export class CateringOrdersComponent extends BaseDirective implements OnInit, OnChanges {
    /** Search filter string */
    @Input() public search: string = '';
    /** Currently selected date */
    @Input() public date: number;
    /** List of catering orders for the selected date */
    public orders: CateringOrder[] = [];
    /** List of catering orders for the selected date */
    public filtered_orders: CateringOrder[] = [];
    /** Whether catering orders are loading */
    public loading: boolean;
    /** List of space IDs for active building */
    public available_spaces: string[] = [];
    /** List of items from an API search */
    public search_results$: Observable<CateringOrder[]>;
    /** Subject holding the value of the search */
    public readonly search$ = new Subject<string>();

    /** Display string for the currenly selected date */
    public get date_display(): string {
        const date = dayjs(this.date);
        return dayjs().isSame(date, 'd') ? 'Today' : date.format('DD MMM YYYY');
    }

    /** Active building */
    public get building(): Building {
        return this._service.Organisation.building;
    }

    constructor(
        private _service: ApplicationService,
        private _route: ActivatedRoute,
        private _dialog: MatDialog
    ) {
        super();
    }

    public ngOnInit(): void {
        this._service.Spaces.initialised.pipe(first((_) => _)).subscribe(() => {
            this.subscription(
                'building',
                this._service.Organisation.listen('active_building').subscribe(() => {
                    this.search$.next(`${this.date}|${this.building.id}`);
                    this.available_spaces = this._service.Spaces.filter(
                        (space) => space.zones.indexOf(this.building.id) >= 0
                    ).map((i) => i.id);
                })
            );
            this.available_spaces = this._service.Spaces.filter(
                (space) => space.zones.indexOf(this.building.id) >= 0
            ).map((i) => i.id);
            this.initBookings();
            this.interval(
                'update_bookings',
                () => this.search$.next(`${this.date}|${this.building.id}|${dayjs().unix()}`),
                30 * 1000
            );
            this.search$.next(`${this.date}|${this.building.id}|${dayjs().unix()}`);
            this.subscription(
                'route.query',
                this._route.queryParamMap.subscribe((params) => {
                    /* istanbul ignore else */
                    if (params.has('view')) {
                        const view_id = params.get('view');
                        const details = localStorage.getItem('CONCIERGE.view_order');
                        const booking_details = localStorage.getItem('CONCIERGE.view_booking');
                        /* istanbul ignore else */
                        if (details) {
                            const booking = new Booking(
                                JSON.parse(booking_details)
                            );
                            const order = new CateringOrder(JSON.parse(details));
                            order.booking = booking;
                            this.viewOrderDetails(order);
                            localStorage.removeItem('CONCIERGE.view_order');
                            localStorage.removeItem('CONCIERGE.view_booking');
                        }
                    }
                })
            );
        });
    }

    public ngOnChanges(changes: SimpleChanges) {
        /* istanbul ignore else */
        if (changes.date && this.building) {
            this.search$.next(`${this.date}|${this.building.id}`);
        }
        /* istanbul ignore else */
        if (changes.search) {
            this.filtered_orders = this.filter(this.orders, this.search);
        }
    }

    /* istanbul ignore next */
    public trackByFn(index: number, order: CateringOrder) {
        return order ? order.id : undefined;
    }

    /** Open modal to view catering order details */
    public viewOrderDetails(order: CateringOrder): void {
        this._dialog.open(OrderDetailsModalComponent, {
            maxHeight: '95vh',
            maxWidth: '95vw',
            data: {
                order: order,
                booking: order.booking,
            },
        });
    }

    /**
     * Filter list to find matching items
     * @param list List of orders to filter
     * @param search Filter string
     */
    private filter(list: CateringOrder[], search: string = '') {
        const filter = search.toLowerCase();
        return list.filter((order) => {
            return order.items.find(
                (item: CateringCategory) =>
                    item.name.toLowerCase().indexOf(filter) >= 0 ||
                    (item.items &&
                        item.items.find((sub_item: CateringCategory) => {
                            return (
                                sub_item.name.toLowerCase().indexOf(filter) >= 0 ||
                                (sub_item.items &&
                                    sub_item.items.find((sub_sub_item) => {
                                        return sub_sub_item.name.toLowerCase().indexOf(filter) >= 0;
                                    }))
                            );
                        }))
            );
        });
    }

    private initBookings() {
        // Listen for input changes
        this.search_results$ = this.search$.pipe(
            debounceTime(100),
            distinctUntilChanged(),
            switchMap((_) => {
                this.loading = true;
                const date = dayjs(this.date).startOf('d');
                return this._service.Spaces.query({
                    zone_ids: this.building.id,
                    available_from: date.unix(),
                    available_to: date.endOf('d').unix(),
                });
            }),
            catchError((_) => of([])),
            map((space_list: Space[]) => {
                let orders: CateringOrder[] = [];
                space_list.forEach((space) => {
                    space.bookings.forEach((booking) => {
                        const catering = booking.catering;
                        catering.forEach((order) => {
                            (order as any).booking_date = booking.date;
                            order.booking = booking;
                        });
                        orders = orders.concat(booking.catering);
                    });
                });
                orders = unique(orders, 'id');
                orders = orders.filter(
                    (order) => this.available_spaces.indexOf(order.location_id) >= 0
                );
                orders.sort(
                    (a, b) =>
                        a.booking_date +
                        a.delivery_time * 60 * 1000 -
                        (b.booking_date + b.delivery_time * 60 * 1000)
                );
                return orders;
            })
        );
        // Process API results
        this.subscription(
            'search_results',
            this.search_results$.subscribe((list) => {
                this.loading = false;
                this.orders = list;
                this.filtered_orders = this.filter(list, this.search);
            })
        );
    }
}
