import { Component, Input, SimpleChanges, OnChanges } from '@angular/core';
import { MatDialog, MatDialogRef } from '@angular/material/dialog';

import { CateringCategory } from 'src/app/services/data/catering/catering-category.class';
import { CateringGroupModalComponent } from '../../overlays/group-modal/group-modal.component';
import { Building } from 'src/app/services/data/organisation/building.class';
import { ApplicationService } from 'src/app/services/app.service';
import { BaseDirective } from 'src/app/shared/base.directive';
import { DialogEvent } from 'src/app/shared/utilities/types.utilities';

@Component({
    selector: 'a-catering-menu-item-list',
    templateUrl: './item-list.component.html',
    styleUrls: ['./item-list.component.scss'],
})
export class CateringMenuItemListComponent extends BaseDirective implements OnChanges {
    /** Category to display details for */
    @Input() public category: CateringCategory;
    /** Toggle for new groups */
    @Input() public package: number;

    /** Currently active building */
    public get building(): Building {
        return this._service.Organisation.building;
    }

    constructor(private _dialog: MatDialog, private _service: ApplicationService) {
        super();
    }

    public ngOnChanges(changes: SimpleChanges) {
        /* istanbul ignore else */
        if (changes.package && this.package) {
            this.openGroupModal();
        }
    }

    public openGroupModal(group?: CateringCategory) {
        /* istanbul ignore else */
        if (!group) {
            group = new CateringCategory({
                parent_id: this.category.id,
                zones: [this.building.id],
            });
        }
        const ref = this._dialog.open(CateringGroupModalComponent, {
            data: { group, parent: this.category },
        });
        this.subscription(
            'category_modal',
            ref.componentInstance.event.subscribe((event: DialogEvent) => {
                /* istanbul ignore else */
                if (event.reason === 'done') {
                    const index = this.category.items.findIndex(
                        (item) => event.metadata.id === item.id
                    );
                    if (index >= 0) {
                        (this.category.items as any[]).splice(index, 1, event.metadata);
                    } else {
                        (this.category.items as any[]).push(event.metadata);
                    }
                }
            })
        );
    }
}
