import { Component, OnInit, Inject, Output, EventEmitter } from '@angular/core';
import { MAT_DIALOG_DATA } from '@angular/material/dialog';

import { CateringOrder, CateringOrderStatus } from 'src/app/services/data/catering/catering-order.class';
import { Booking } from 'src/app/services/data/bookings/booking.class';
import { ApplicationService } from 'src/app/services/app.service';
import { Space } from 'src/app/services/data/spaces/space.class';
import { DialogEvent, Identity } from 'src/app/shared/utilities/types.utilities';
import { BaseDirective } from 'src/app/shared/base.directive';

import * as dayjs from 'dayjs';
import { IDataClassEvent } from 'src/app/services/data/base-api.class';

export interface OrderDetailsModalData {
    /** Order to display details for */
    order: CateringOrder;
    /** Booking associated with catering order */
    booking: Booking;
}

@Component({
    selector: 'a-order-details-modal',
    templateUrl: './order-details-modal.component.html',
    styleUrls: ['./order-details-modal.component.scss'],
})
export class OrderDetailsModalComponent extends BaseDirective implements OnInit {
    /** Emitter for user action on the modal */
    @Output() public event = new EventEmitter<DialogEvent>();
    /** Whether status is being updated */
    public loading: boolean;
    /** Actively selected status */
    public active_status: CateringOrderStatus;
    /** List of available order statuses */
    public statuses: Identity[] = [
        { id: 'accepted', name: 'Accepted', icon: { class: 'custom-accepted' }, colour: '#3996B6' },
        {
            id: 'preparing',
            name: 'Preparing',
            icon: { class: 'custom-preparing' },
            colour: '#E7536B',
        },
        { id: 'ready', name: 'Ready', icon: { class: 'custom-ready' }, colour: '#FFD028' },
        {
            id: 'delivered',
            name: 'Delivered',
            icon: { class: 'custom-delivered' },
            colour: '#75BB43',
        },
        {
            id: 'cancelled',
            name: 'Cancelled',
            icon: { class: 'custom-cancelled' },
            colour: '#747474',
        },
    ];

    /** Active booking to display */
    public get booking(): Booking {
        return this._data.booking;
    }
    public set booking(bkn: Booking) {
        this._data.booking = bkn;
    }

    /** Order to display */
    public get order(): CateringOrder {
        return this._data.order;
    }

    /** Details for the active status */
    public get status_details(): Identity {
        return this.statuses.find((status) => status.id === this.active_status);
    }

    /** Space associated with the order */
    public get space(): Space {
        return this._service.Spaces.find(this._data.order.location_id);
    }

    /** Details about the delivery time */
    public get time(): string {
        const date = dayjs(this._data.booking.date).add(this._data.order.delivery_time, 'm');
        return `(${date.format('DD MMM YYYY')}) To be delivered at ${this._data.order.deliver_at_time}`;
    }

    constructor(
        private _service: ApplicationService,
        @Inject(MAT_DIALOG_DATA) private _data: OrderDetailsModalData
    ) {
        super();
    }

    public ngOnInit(): void {
        this.active_status = this._data.order.status;
        const handleChange = (event: IDataClassEvent<Booking>) => {
            if (event.type === 'item_saved') {
                this.booking = event.metadata;
                this.subscription('booking_changes', this.booking.changeEvents.subscribe(handleChange));
            }
        };
        this.subscription('booking_changes', this.booking.changeEvents.subscribe(handleChange));
    }

    /** Update the status of the order in the booking */
    public async updateStatus(): Promise<void> {
        const booking = this._data.booking;
        /* istanbul ignore else */
        if (booking) {
            this.loading = true;
            const catering = [...booking.catering];
            catering.splice(
                catering.findIndex((order) => order.id === this._data.order.id),
                1,
                new CateringOrder({ ...this._data.order, status: this.active_status })
            );
            booking.storePendingChange('catering', catering);
            await booking.save().catch((err) => {
                this._service.notifyError(
                    `Error updating order status. Error: ${err.message || err}`
                );
                this.active_status = this._data.order.status;
                this.loading = false;
            });
            this.loading = false;
        }
    }
}
