import { Component, OnInit, Input, SimpleChanges, EventEmitter, Output } from '@angular/core';
import { ApplicationService } from 'src/app/services/app.service';
import { Building } from 'src/app/services/data/organisation/building.class';
import { BaseDirective } from 'src/app/shared/base.directive';
import { CateringCategory } from 'src/app/services/data/catering/catering-category.class';
import { MatDialog } from '@angular/material/dialog';
import { CateringCategoryModalComponent } from '../overlays/category-modal/category-modal.component';
import { HashMap, DialogEvent } from 'src/app/shared/utilities/types.utilities';

@Component({
    selector: 'a-catering-menu',
    templateUrl: './menu.component.html',
    styleUrls: ['./menu.component.scss'],
})
export class CateringMenuComponent extends BaseDirective implements OnInit {
    /** Filter string for catering items */
    @Input() public search: string;
    /** Opens new category modal on change */
    @Input() public category: number;
    /** Opens new category modal on change */
    @Input() public package: number;
    /** Emitter for changes to the length of catering categories */
    @Output() public length = new EventEmitter<number>();
    /** List of top level catering categories */
    public catering_menu: CateringCategory[] = [];
    /** Filtered list of top level catering categories */
    public filtered_menu: CateringCategory[] = [];
    /** Whether menu details are being loaded */
    public loading: boolean;
    /** Index of the active tab */
    public index: number = 0;
    /** Map of indexes to package open state */
    public state: HashMap<number> = {};

    /** Currently active building */
    public get building(): Building {
        return this._service.Organisation.building;
    }

    constructor(private _service: ApplicationService, private _dialog: MatDialog) {
        super();
    }

    public ngOnInit(): void {
        this.subscription(
            'building',
            this._service.Organisation.listen('active_building').subscribe((_) => {
                this.index = 0;
                this.catering_menu = [];
                this.filtered_menu = [];
                this.loadCateringMenu();
            })
        );
    }

    public ngOnChanges(changes: SimpleChanges) {
        if (changes.search) {
            this.filtered_menu = this.filter(this.catering_menu);
        }
        if (changes.category && this.category) {
            this.openCategoryModal();
        }
        if (changes.package) {
            this.state[this.index] = this.package;
        }
    }

    public openCategoryModal(category?: CateringCategory) {
        /* istanbul ignore else */
        if (!category) {
            category = new CateringCategory({ zones: [this.building.id] });
        }
        const ref = this._dialog.open(CateringCategoryModalComponent, { data: { category } });
        this.subscription('category_modal', ref.componentInstance.event.subscribe((event: DialogEvent) => {
            /* istanbul ignore else */
            if (event.reason === 'done') {
                const index = this.catering_menu.findIndex(item => event.metadata.id === item.id);
                if (index >= 0) {
                    this.catering_menu.splice(index, 1, event.metadata);
                    this.index = index;
                } else {
                    this.index = this.catering_menu.length;
                    this.catering_menu.push(event.metadata);
                }
            }
        }));

    }

    private loadCateringMenu(): void {
        /* istanbul ignore else */
        if (this.building) {
            this.loading = true;
            this._service.Menu.query({ zone_id: this.building.id }).then(
                (menu: any[]) => {
                    this.catering_menu = (menu || []).map((item) => new CateringCategory(item));
                    this.length.emit(this.catering_menu.length);
                    this.filtered_menu = this.filter(this.catering_menu);
                    this.loading = false;
                },
                (err) => {
                    this._service.notifyError(
                        `Error loading menu for ${this.building.name}. Error: ${err.message | err}`,
                        'Retry',
                        () => this.loadCateringMenu()
                    );
                    this.loading = false;
                }
            );
        }
    }

    /** Filter the list of catering items */
    private filter(list: CateringCategory[]) {
        return list;
    }
}
